import { formateDate } from '@/global/utils.js';

import tTable from '@/components/t-table/t-table.vue';
import tTh from '@/components/t-table/t-th.vue';
import tTr from '@/components/t-table/t-tr.vue';
import tTd from '@/components/t-table/t-td.vue';
import {dogetscorelist} from '@/api/supervisor/manager.js'

export default {
	components: {
		tTable,
		tTh,
		tTr,
		tTd
	},
	data: () => {
		let today = formateDate(new Date(), 'Y-M-D');
		return {
			tableList: [],
			query: {
				totalCount: 0,
				pageSize: 10,
				currentPage: 1,
				username: '',
				startdate: today,
				materialTypeUuid: '',
				auditstate: -1,
				sort: [
					{
						direct: 'DESC',
						field: 'ID'
					}
				]
			},
			allCheck: false
		};
	},
	methods: {
		change(e) {
			console.log(e.detail);
		},
		edit(item) {
			uni.showToast({
				title: item.name,
				icon: 'none'
			});
		},

		StartDateChange(e) {
			this.query.startdate = e.detail.value;
			this.getdatalist();
			
		},
		getdatalist(){
			var parms={uuid:this.$store.state.userId,addtime:this.query.startdate};
			console.log(parms);
			dogetscorelist(parms).then(res=>{
				if(res.data.code==200){
					this.tableList=res.data.data;
					console.log(this.tableList);
				}
			})
		}
	},
	created() {
		let today = formateDate(new Date(), 'Y-M-D');
		this.query.startdate=today;
		this.getdatalist();
	},
	mounted() {},
	computed: {}
};
